//BrowserContextProvider -- https://github.com/facebook/docusaurus/blob/main/packages/docusaurus/src/client/browserContext.tsx
//BrowserOnly component -- https://github.com/facebook/docusaurus/blob/main/packages/docusaurus/src/client/exports/BrowserOnly.tsx
//useIsBrowser hook -- https://github.com/facebook/docusaurus/blob/main/packages/docusaurus/src/client/exports/useIsBrowser.ts
//The perils of rehydration -- https://www.joshwcomeau.com/react/the-perils-of-rehydration/#abstractions

/*global process*/

import React, { useEffect, useState } from 'react';

export default function BrowserOnly({ children, fallback }) {
	const [isBrowser, setIsBrowser] = useState(false);

	useEffect(() => {
		setIsBrowser(true);
	}, []);

	if (isBrowser) {
		// if (typeof children !== 'function') {
		if (typeof children !== 'function' && process.env.NODE_ENV === 'development') {
			//https://www.gatsbyjs.com/docs/how-to/local-development/environment-variables/

			throw new Error(
				'The children of <BrowserOnly> must be a "render function", e.g. <BrowserOnly>{() => <span>{window.location.href}</span>}</BrowserOnly>',
			);
		}
		return <>{children?.()}</>;
	}

	// return null
	return fallback ?? null;
}
