import React from 'react';
import clsx from 'clsx';
import { Link } from 'gatsby';
import Icon from '@components/ui/Icon';

// import './button.css';
import * as styles from './button.module.css';

export default function Button({ id, type, variant, size, onClick, isDisabled, isWip, wipText, children, icon, isIconOnRight, ...restProps }) {
	return (
		<button
			tabIndex="0"
			id={id}
			// className={className?'btn '+className:'btn'}
			className={clsx(
				styles.btn, //'button',
				variant == 'secondary'
					? styles.secondary //'button--secondary'
					: variant == 'tertiary'
					? styles.tertiary //'button--tertiary'
					: styles.primary, //'button--primary'
				isWip && styles.wip,
				size == 'small' && styles.small,
				size == 'smaller' && styles.smaller,
				size == 'big' && styles.big,
				size == 'bigger' && styles.bigger,
				icon && icon !== '' && styles.ico,
				icon && icon !== '' && isIconOnRight && styles.icoRight,
				/* 'button',
				type=='secondary' ? 
					'button--secondary'
					:
					type=='tertiary' ?
						'button--tertiary'
						:
						'button--primary'
				,
				size=='small' && 'button--sm',
				size=='large' && 'button--lg' */
			)}
			type={type || 'button'}
			onClick={onClick}
			disabled={isDisabled || isWip ? 'disabled' : ''}
			{...restProps}
		>
			{/* {isWip ?
				wipText.trim() !== '' ? wipText.trim() : 'Loading...'
				:
				<span>{children || 'Button'}</span>
			} */}
			<>
				{!isWip && icon && icon !== '' && <Icon icon={icon} />}
				{children && <span>{isWip ? (wipText.trim() !== '' ? wipText.trim() : 'Loading...') : children}</span>}
			</>
		</button>
	);
}

export function LinkButton({ to, variant, size, children, icon, isIconOnRight, ...restProps }) {
	//if it is a normal URL, use 'a' anchor tag, else use gatsby 'Link'
	if (to.startsWith('http')) {
		return (
			<a
				tabIndex="0"
				className={clsx(
					styles.btn, //'button',
					variant == 'secondary'
						? styles.secondary //'button--secondary'
						: variant == 'tertiary'
						? styles.tertiary //'button--tertiary'
						: styles.primary, //'button--primary'
					size == 'small' && styles.small,
					size == 'smaller' && styles.smaller,
					size == 'big' && styles.big,
					size == 'bigger' && styles.bigger,
					icon && icon !== '' && styles.ico,
					icon && icon !== '' && isIconOnRight && styles.icoRight,
				)}
				href={to}
				{...restProps}
			>
				<>
					{icon && icon !== '' && <Icon icon={icon} />}
					{children && <span>{children}</span>}
				</>
			</a>
		);
	} else {
		return (
			<Link
				tabIndex="0"
				className={clsx(
					styles.btn, //'button',
					variant == 'secondary'
						? styles.secondary //'button--secondary'
						: variant == 'tertiary'
						? styles.tertiary //'button--tertiary'
						: styles.primary, //'button--primary'
					size == 'small' && styles.small,
					size == 'smaller' && styles.smaller,
					size == 'big' && styles.big,
					size == 'bigger' && styles.bigger,
					icon && icon !== '' && styles.ico,
					icon && icon !== '' && isIconOnRight && styles.icoRight,
					/* 'button',
					variant=='secondary' ? 
						'button--secondary'
						:
						variant=='tertiary' ?
							'button--tertiary'
							:
							'button--primary'
					,
					size=='small' && 'button--sm',
					size=='large' && 'button--lg' */
				)}
				to={to}
				{...restProps}
			>
				<>
					{icon && icon !== '' && <Icon icon={icon} />}
					{children && <span>{children}</span>}
				</>
				{/* {children || 'Button'} */}
			</Link>
		);
	}
}

export function ButtonListContainer({ children }) {
	return <div className={styles.btnCntnr}>{children}</div>;
}
