import React from 'react';
// import clsx from 'clsx';

// import * as styles from './pan.module.css';
import { hPan } from './pan.module.css';
import { hPan_touchDevice } from './touchDeviceHPan.module.css';

export default function HorizontalPan({ children, panOnTouchDeviceOnly, className }) {
	return (
		// <div className={clsx(styles.hPanCntnr)}>
		<div className={panOnTouchDeviceOnly ? hPan_touchDevice : hPan}>
			<ul className={className || ''}>
				{children.map((child, i) => (
					<li key={i}>{child}</li>
				))}
			</ul>
		</div>
	);
}
