import React from 'react';

import { graphql, useStaticQuery } from 'gatsby';

/** @returns {{siteName,title,description,favicon,featuredImage,themeColor,siteUrl,instagramUrl}} Meta Data */
export default function useSiteMetadata(props) {
	const data = useStaticQuery(graphql`
		query {
			site {
				siteMetadata {
					siteName
					title
					description
					favicon
					featuredImage
					themeColor
					siteUrl
					instagramUrl
				}
			}
		}
	`);

	return data.site.siteMetadata;
}
