import React from 'react';
import Footer from '@components/Footer';
import Header from '@components/Header';
//import Seo from "./seo";

/** Full width layout
 * @param {Object} props
 * @param {boolean} [props.hideFooter=false] Whether to hide footer. Default false
 */
export default function FullWidthPageLayout({ children, hideFooter, ...restProps }) {
	return (
		<>
			{/* <Seo></Seo> */}
			<Header />
			<main {...restProps}>{children}</main>
			{!hideFooter && <Footer />}
		</>
	);
}
