import React from 'react';

import Lottie from 'lottie-react';
import confettiSuccessTick from './confettiSuccessTick.json';

export default function LottieConfettiSuccessTick({ loop }) {
	return (
		<Lottie
			animationData={confettiSuccessTick}
			loop={loop == undefined ? true : loop} //default true
			//autoplay={true}
		/>
	);
}
