import React from 'react';
import { Link } from 'gatsby';
import HorizontalPan from '@containers/HorizontalPan';
import { LinkButton } from '@components/ui/Button';
import * as styles from './footer.module.css';

export default function Footer() {
	return (
		<footer id={styles.ftr}>
			<HorizontalPan panOnTouchDeviceOnly={true} className={styles.ftrRow}>
				<div>
					<h3>Darkkpool</h3>
					<ul>
						<li>
							<Link activeClassName="active" to="/about">
								About Darkkpool AI
							</Link>
						</li>
						<li>
							<Link activeClassName="active" to="/products">
								Products
							</Link>
						</li>
						<li>
							<Link activeClassName="active" to="/products#frequently-asked-questions">
								FAQs
							</Link>
						</li>
						{/* <li>
							<Link activeClassName="active" to="/terms-of-use">
								Terms Of Use
							</Link>
						</li> */}
					</ul>
				</div>
				<div>
					<h3>More</h3>
					<ul>
						{/* <li>
							<Link activeClassName="active" to="/privacy-policy">
								Privacy Policy
							</Link>
						</li> */}
						{/* <li>
							<Link activeClassName="active" to="/cookie-policy">
								Cookie Policy
							</Link>
						</li> */}
						<li>
							<Link activeClassName="active" to="/contact">
								Contact
							</Link>
						</li>
						<li>
							<Link activeClassName="active" to="/welcome">
								Enroll
							</Link>
						</li>
					</ul>
				</div>
				<ul>
					<li>
						{/* <LinkButton variant="secondary" target="_blank" to="https://t.me/Lunaticnifty50_bot">
							Access Tool
						</LinkButton> */}
						<img src="/img/logo.webp" alt="Darkkpool" />
					</li>
					<li>
						<LinkButton variant="primary" to="/contact">
							Contact Us
						</LinkButton>
					</li>
				</ul>
			</HorizontalPan>
			<div>copyright &copy; 2024-2025, DarkkPool Tech Pvt. Ltd.</div>
		</footer>
	);
}
