import React from 'react';
// import clsx from 'clsx';
import { Link } from 'gatsby';
import Icon from '@components/ui/Icon';
import { logoC as logoC_ID, active as activeCssClass, hdr as hdrId, navMenu as navMenuId } from './header.module.css';
// import logo from '../../images/logo.png';

export default function Header() {
	return (
		<header id={hdrId}>
			{/* <Link to="/"><img alt="Nimworks" src={logo} /></Link> */}
			<div id={logoC_ID}>
				<img src="/img/logo.webp" alt="Darkkpool" />
				<span>Darkkpool</span>
			</div>
			<nav id={navMenuId}>
				<ul>
					<li>
						<Link activeClassName={activeCssClass} to="/">
							<Icon icon="home" />
							<span>Home</span>
						</Link>
					</li>
					<li>
						<Link activeClassName={activeCssClass} to="/products">
							<Icon icon="diamond" />
							<span>Products</span>
						</Link>
					</li>
					{/* <li>
						<Link activeClassName={activeCssClass} to="/pricing">
							<Icon icon="rupee-sign" />
							<span>Pricing</span>
						</Link>
					</li> */}
					<li>
						<Link activeClassName={activeCssClass} to="/about">
							<Icon icon="alert-circle" />
							<span>About</span>
						</Link>
					</li>
					<li>
						<Link activeClassName={activeCssClass} to="/contact">
							<Icon icon="phone" />
							<span>Contact</span>
						</Link>
					</li>
				</ul>
			</nav>
			{/*<span>Search</span>*/}
		</header>
	);
}

