import React from 'react';
import clsx from 'clsx';

import * as styles from './checkbox.module.css';

export default function Checkbox({ className, children, isChecked, onToggle, isError }) {
	return (
		<div className={clsx(styles.chk, isChecked && styles.y, isError && styles.err, className && className.trim() !== '' && className)} onClick={onToggle}>
			{/*<span onClick={onToggle}/>*/}
			<span />
			<span>{children}</span>
		</div>
	);
}
