import React, { useContext, useEffect, useRef, useState } from 'react';
// import clsx from 'clsx';
import HCaptcha from '@hcaptcha/react-hcaptcha';

import Button, { LinkButton } from '@components/ui/Button';
import TextInput from '@components/ui/TextInput';
import Checkbox from '@components/ui/Checkbox';
import Loading from '@components/ui/Loading';
import LottieConfettiSuccessTick from '@components/lottieAnims/ConfettiSuccessTick';

import { ToastNotificationContext } from '@providers/ToastNotification';
import { NetworkCheckContext } from '@providers/NetworkAvailabilityCheck';

import * as styles from './questionnaire.module.css';

const cryptoCoins = ['Bitcoin', 'Etherium', 'Shiba Inu', 'Doge coin'];

export default function VisitorQuestionnaire() {
	const { notify } = useContext(ToastNotificationContext);
	const isOnline = useContext(NetworkCheckContext);

	const [name, setName] = useState('');
	const [nameErr, setNameErr] = useState('');
	const [email, setEmail] = useState('');
	const [emailErr, setEmailErr] = useState('');
	// const [phn, setPhn] = useState('');
	// const [phnErr, setPhnErr] = useState('');
	const [dob, setDob] = useState('');
	const [dobErr, setDobErr] = useState('');

	const [knownCryptoCoins, setKnownCryptoCoins] = useState([]);

	const [captchaToken, setCaptchaToken] = useState(null);

	const [isWip, setIsWip] = useState(false);
	const [step, setStep] = useState(1);

	/////
	const validateAndSetName = (inputVal) => {
		var err = getNameErrs(inputVal, true);
		setNameErr(err ? err : '');
		inputVal !== name && setName(inputVal);
		return err == null; //if no errors, then we return 'true'
	};
	const validateAndSetEmail = (inputVal) => {
		var err = getEmailErrors(inputVal, true);
		setEmailErr(err ? err : '');
		inputVal !== email && setEmail(inputVal);
		return err == null; //if no errors, then we return 'true'
	};
	// const validateAndSetPhn = (inputVal) => {
	// 	var err = getPhoneErrors(inputVal, true);
	// 	setPhnErr(err ? err : '');
	// 	inputVal !== phn && setPhn(inputVal);
	// 	return err == null; //if no errors, then we return 'true'
	// };
	const validateAndSetDob = (inputVal) => {
		const err = getDobErrors(inputVal);
		setDobErr(err ? err : '');
		inputVal !== dob && setDob(inputVal);
		return err == null; //if no errors, then we return 'true'
	};
	/////

	const toggleKnownCoinsInArray = (knownCoin) => {
		//we clone array since we shall be setting state and the state needs to be immutable
		const clonedAry = [...knownCryptoCoins]; //Array.from(knownCryptoCoins);
		const index = clonedAry.indexOf(knownCoin);
		if (index === -1) {
			clonedAry.push(knownCoin); // Item not found, add it to the array
		} else {
			clonedAry.splice(index, 1); // Item exists, remove it from the array
		}
		// return clonedAry;
		setKnownCryptoCoins(clonedAry);
	};

	const maxSteps = 4;
	const goNext = async () => {
		if (step >= maxSteps) return false;
		switch (step) {
			case 1: //if we are at step 1 and moving to step 2
				// if (!validateAndSetName(name) || !validateAndSetEmail(email) || !validateAndSetPhn(phn)){
				var isNameValid = validateAndSetName(name),
					isEmailValid = validateAndSetEmail(email),
					// isPhnValid = validateAndSetPhn(phn),
					isDobVaild = validateAndSetDob(dob);
				// if (!isNameValid || !isEmailValid || !isPhnValid) {
				if (!isNameValid || !isEmailValid || !isDobVaild) {
					notify('Please provide required details', 'err');
					return false;
				}
				// notify(<LottieConfettiSurprise loop={false} />);
				notify('Hello, ' + name);
				setStep(2); //we go to next step
				break;
			// case 2: //if we are at step 2 and moving to step 3
			// 	const dobValidationErr = validateAndSetDob(dob);
			// 	if (dobValidationErr) {
			// 		notify(dobValidationErr, 'err');
			// 		return false;
			// 	}
			// 	setStep(3); //we go to next step
			// 	break;
			case 2: //if we are at step 2 and moving to step 3
				if (knownCryptoCoins.length > 0) {
					notify('Great to know you are aware about crypto coins!');
				}
				setStep(3); //we go to next step
				break;
			case 3: //if we are at step 3 and moving to step 4
				// console.log('present captcha here and submit to backend');
				if (isWip) return false;
				if (captchaToken == null) {
					notify('Kindly solve captcha', 'err');
					return false;
				}
				if (!isOnline) {
					notify('You are offline! Kindly check network connection', 'alert');
					return false;
				}
				//prep data to send to backend
				var dataToSend = {
					n: name,
					eml: email,
					// phn: phn,
					dob: dob,
					// kc: knownCryptoCoins.join(','),
					cpt: captchaToken,
				};
				//optional data to send
				var knownCryptoCoins_sanitized = knownCryptoCoins.filter((coin) => cryptoCoins.includes(coin));
				if (knownCryptoCoins_sanitized.length > 0) {
					dataToSend['kc'] = knownCryptoCoins_sanitized.join(',');
				}

				try {
					setIsWip(true);
					//https://stackoverflow.com/questions/53433938/how-do-i-allow-a-cors-requests-in-my-google-script
					const rawResp = await fetch(
						// 'https://script.google.com/macros/s/AKfycbxi166CCt6l9N4kubyYjIbQbXoiHcsxCY7JGzCE6EXOxdwBRnlmFLIdY7beGgUkupw/exec', //nimish.sr gmail
						// 'https://script.google.com/macros/s/AKfycbyJq2xS3ewVJxFuVUJdwZ6B2l7nkgc5t18zHVcRTyQPtZbwMHQMFasJcN4Bi794MRL5fQ/exec', //ritesh's gmail
						'https://script.google.com/macros/s/AKfycbyh-KTHUA8XuoxelzpwButWu4atV7D978I_gERB6K07Cyv84bBoSN5vlHjXl5yVo_URDA/exec',
						{
							redirect: 'follow',
							method: 'POST',
							headers: {
								// Accept: 'application/json',
								// 'Content-Type': 'application/json',
								'Content-Type': 'text/plain;charset=utf-8',
							},
							body: JSON.stringify(dataToSend),
						},
					);
					const resp = await rawResp.json();
					// console.log('resp', resp);
					if (!Object.prototype.hasOwnProperty.call(resp, 'status')) throw new Error();
					if (resp.status === 0) throw new Error(resp.error || 'Failed to Enroll, Please retry');
					if (resp.status !== 1) throw new Error();
					// console.log('Send response here');
					//////
					// console.log('resp', resp);

					// if (Object.prototype.hasOwnProperty.call(resp, 'rwrd') && resp.rwrd) {
					// 	//check if user earned a reward
					// 	if (resp.rwrd === 't' || resp.rwrd === 'cc') setReward(resp.rwrd); //if reward is either Tea or ColdCoffee
					// 	notify('You received a lucky reward!', 'success');
					// } else {
					// 	notify('Thank you for enrolling!', 'success'); //Scratch to win
					// }

					// //save to localstorage/cookie and show success page,
					// const cookieVal = encodeURIComponent(JSON.stringify([name, email, reward]));
					// document.cookie = `vqj=${cookieVal};expires=${new Date(Date.now() + 604800000).toGMTString()};max-age=604800;SameSite=Strict; Secure`;
					// // WE ALSO WRITE HTTP-ONLY COOKIE FROM SERVER-SIDE

					setStep(4);
					notify('Thank you for reaching out!', 'success'); //Scratch to win

					//reset all data OR save to localstorage
				} catch (err) {
					// console.error(err);
					notify(err.message || err.name || err.code || 'Failed to Enroll, Please retry', 'err');
					setIsWip(false);
				} finally {
					setIsWip(false);
				}
				break;
		}
	};

	const goPrev = () => {
		step > 1 && setStep(step - 1);
		setCaptchaToken(null);
	};

	return (
		<>
			<div className={styles.qC}>
				<div className={styles.qHdr}>
					<h3>
						{step == 4
							? 'Thank You, ' + name.split(' ')[0]
							: step == 3
								? 'Human verification'
								: step == 2
									? 'Which crypto coins are you aware of?'
									: 'Welcome!'}
					</h3>
					{(step == 1 || step == 3 || step == 4) && (
						<span>
							{step == 4
								? 'We shall revert to you shortly!'
								: step == 3
									? captchaToken == null
										? 'Verify that you are a human!'
										: 'Verified! Kindly proceed'
									: 'Let us get to know you'}
						</span>
					)}
				</div>
				{step == 1 && (
					<>
						<TextInput
							title="Name"
							placeholder="Name"
							onChange={(e) => {
								validateAndSetName(e.target.value);
								// setName(e.target.value);
							}}
							value={name}
							error={nameErr}
							icon="user"
							initFocused={true}
						/>
						<TextInput
							title="Email"
							placeholder="Email"
							onChange={(e) => {
								validateAndSetEmail(e.target.value);
								// setEmail(e.target.value);
							}}
							value={email}
							error={emailErr}
							icon="mail"
						/>
						<TextInput
							title="Date of Birth"
							onChange={(e) => {
								// console.log(e.target.value);
								// setDob(e.target.value);/
								validateAndSetDob(e.target.value);
							}}
							value={dob}
							error={dobErr}
							type="date"
						/>
					</>
				)}
				{step == 2 &&
					cryptoCoins.map((coin) => (
						<Checkbox
							key={coin}
							isChecked={knownCryptoCoins.includes(coin)}
							onToggle={() => {
								// setIsIndStkMktKnown(!isIndStkMktKnown);
								toggleKnownCoinsInArray(coin);
							}}
						>
							{coin}
						</Checkbox>
					))}
				{step == 3 && <HumanVerification captchaToken={captchaToken} setCaptchaToken={setCaptchaToken} notify={notify} />}
				{step == 4 && (
					<div>
						<LottieConfettiSuccessTick loop={false} />
					</div>
				)}
				<div className={styles.qCtaC}>
					{step == maxSteps ? (
						<LinkButton to="/" variant="primary" icon="alert-circle" isIconOnRight={true}>
							Learn More
						</LinkButton>
					) : (
						// <Button variant='secondary' size='small' onClick={() => { console.log('View Form'); }} icon='arrow-right' isIconOnRight={true}>View Form</Button>
						<>
							<Button variant="primary" size="small" onClick={goNext} icon="arrow-right" isIconOnRight={true}>
								{step == maxSteps - 1 ? 'Submit' : 'Proceed'}
							</Button>
							{step > 1 && <Button variant="secondary" type="button" onClick={goPrev} icon="arrow-left"></Button>}
						</>
					)}
				</div>
			</div>
			{isWip && <Loading note="Enrolling" />}
			{/* {!isLoadingDone && <Loading note="Loading" />} */}
		</>
	);
}

//======================+
//		HELPER FNS		|
//======================+
function getNameErrs(name, isReq) {
	if (isReq == undefined) isReq = true;
	if (isReq && (!name || name.trim() == '')) return 'Required';
	if (isReq && name.trim().length < 3) return 'Minimum 3 letters';
	if (name.trim().length > 40) return 'Is too long';
	if (/[^a-zA-Z ]/.test(name)) return 'Only alphabets allowed';
	// matches the format 'firstname lastname'
	// if(!/^[^\d\W\s_]+(?:(?:[^\d\W_]{2,})(?:[\s]{1})[^\d\W\s_]{2,}){0,1}$/.test(name))return 'Enter valid Name';
	// /^([^\d\W\s_]{3,20})(?:[\s]{1}([^\d\W\s_]{2,20})){0,1}$/			<-- this pattern captures the 'firstname' & 'lastname' in the match-group array
	if (!/^(?:[^\d\W\s_]{3,20})(?:[\s]{1}(?:[^\d\W\s_]{2,20})){0,1}$/.test(name)) return 'Req Format: Firstname Lastname';
	return null;
}

function getEmailErrors(eml, isReq) {
	if (isReq == undefined) isReq = true;
	if (isReq && (!eml || eml.trim() == '')) return 'Email required';
	if (eml.length > 60) return 'Is too long';
	if (eml.trim() !== '' && !/^[\w.@]+@+[\w.]+$/.test(eml)) return 'Enter valid email';
	return null;
}

/* function getPhoneErrors(phn, isReq) {
	if (isReq == undefined) isReq = true;
	if (isReq && (!phn || phn.trim() == '')) return 'Phone required';
	// if(/[^0-9]/.test(phn))return 'Only numbers allowed';

	if (phn.length > 0 && !/^(?:\+|\d{1,4})\s?\d{10}$/.test(phn)) return 'Enter valid phone number'; // Regex to validate international phone numbers
	// if (phn.length > 0 && !/^[0-9]{10}$/.test(phn)) return 'Enter valid 10 digit number';
	if (phn.length > 0 && phn.trim().length !== 10) return 'Should be 10 digits long';
	return null;
} */

function getDobErrors(inputVal) {
	if (!inputVal || inputVal.trim() === '') return 'Required'; // Check if input is empty
	const today = new Date();
	const inputDate = new Date(inputVal); // Parse the input date
	if (isNaN(inputDate.getTime())) return 'Invalid date format'; // Check if input is a valid date
	if (inputDate > today) return 'Only past date allowed'; // Check if the date is in the future

	// Calculate the age
	const ageDiff = today.getFullYear() - inputDate.getFullYear();
	const hasHadBirthdayThisYear =
		today.getMonth() > inputDate.getMonth() || (today.getMonth() === inputDate.getMonth() && today.getDate() >= inputDate.getDate());
	const age = hasHadBirthdayThisYear ? ageDiff : ageDiff - 1;
	if (age < 18) return 'Age must be 18+'; // Check if the user is at least 18 years old	//You must be at least 18 years old

	return null; // All validations passed
}

